import React, {useEffect, useState} from 'react';
import { connect } from 'react-redux';
import { Divider, Table, Input } from "antd";
import { changLoadingState } from '../../actions/global';
import RecommendedArticles from '../../components/RecommendedArticles';
import { recommendedArticleArr, allBlogLink } from '../../utils/enums';

const { Search } = Input;

const mapState = state => ({
    loading: state.global.isLoading,
});
function Index(props) {
    const [dataSource, setDataSource] = useState(allBlogLink);
    const columns = [
        {
            title: '',
            dataIndex: 'id',
            key: 'id',
            width: 80,
        }, {
            title: '',
            dataIndex: 'name',
            key: 'name',
            render: (text, item) => <a href={item.link} target="_blank">{text}</a>,
        },
    ];
    useEffect(() => {
        props.changLoadingState();
        return () => props.changLoadingState();
    }, [])

    return (
        <div>
            <RecommendedArticles
                recommendedArticleArr={recommendedArticleArr}
            />
            <div style={{marginTop: 40}}></div>
            <Divider orientation="left"></Divider>
            <Search
                placeholder=""
                onSearch={value => {
                    const newDataSource = allBlogLink.filter(item => (item.name).includes(value));
                    setDataSource(newDataSource)
                }}
                style={{ width: 200 }}
            />
            <Table columns={columns} dataSource={dataSource} />
        </div>
    );
}

export default connect(mapState, { changLoadingState })(Index);



